import React from 'react';

import imgSrc from '../../../public/images/service-section8.png';

export default () => (<div className="section_9">
  <div className="content">
    <div className="left"><img src={imgSrc} alt="" title="" /></div>
    <div className="right">
      <h3>Secure</h3>
      <h2>Brandable</h2>
      <p>The user interface can be designed and branded as a fully white labeled service, using your own logos and domains.</p>
    </div>
  </div>
</div>)


